import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AboutComponent } from './components/about/about.component';
import { ContactComponent } from './components/contact/contact.component';
// import { IntroComponent } from './components/intro/intro.component';
import { MainComponent } from './components/main/main.component';
import { BannerComponent } from './components/products/grafica/banner/banner.component';
import { CardsComponent } from './components/products/grafica/cards/cards.component';
import { FlyerComponent } from './components/products/grafica/flyer/flyer.component';
import { LogoComponent } from './components/products/grafica/logo/logo.component';
import { ProductsComponent } from './components/products/products.component';
import { AplicatiiComponent } from './components/products/web/aplicatii/aplicatii.component';
import { PortofoliuComponent } from './components/products/web/portofoliu/portofoliu.component';
import { SiteDePrezentareComponent } from './components/products/web/site-de-prezentare/site-de-prezentare.component';
import { InCursComponent } from './components/proiecte/in-curs/in-curs.component';
import { TerminateComponent } from './components/proiecte/terminate/terminate.component';



const routes: Routes = [
  // { path: 'intro', component: IntroComponent },
  { path: '', component: MainComponent },
  { path: 'about', component: AboutComponent },
  { path: 'products', component: ProductsComponent },
  { path: 'contact', component: ContactComponent },

  { path: 'portofoliu', component: PortofoliuComponent },
  { path: 'site-prezentare', component: SiteDePrezentareComponent },
  { path: 'aplicatii', component: AplicatiiComponent },

  { path: 'cards', component: CardsComponent },
  { path: 'flyer', component: FlyerComponent },
  { path: 'logo', component: LogoComponent },
  { path: 'banner', component: BannerComponent },

  { path: 'terminate', component: TerminateComponent },
  { path: 'incurs', component: InCursComponent },





];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
